function height (el) {
	var height = 0;
	$(el).each(function () {
		var thisHeight = $(this).height();
		if (thisHeight > height) {
			height = thisHeight;
		}
		setTimeout(() => {
			$(el).height(height)
		}, 100)
	})
}
let header = {
	scrollActive: function () {
		let height = $('header').height()
		if ($(window).scrollTop() > height) {
			$('header').addClass('active')
		} else {
			$('header').removeClass('active')
		}
	},
	checkHeightHeader: function () {
		let height = $('header').outerHeight()
		let menuMobile = $('header .mobile-wrap')
		// menuMobile.css('padding-top', height)
		$('main').css('padding-top', height)

	},
}

function setBackgroundElement () {
	$('[setBackground]').each(function () {
		var background = $(this).attr('setBackground')
		$(this).css({
			"background-image": "url(" + background + ")",
			"background-size": "cover",
			"background-position": "center center"
		})
	})
	$('[setBackgroundRepeat]').each(function () {
		var background = $(this).attr('setBackgroundRepeat')
		$(this).css({
			"background-image": "url(" + background + ")",
			"background-repeat": "repeat"
		})
	})
}

function mappingMenu () {
	var menu = $('header .menu-list').mapping({
		mobileWrapper: 'header .mobile-wrap',
		mobileMethod: 'appendTo',
		desktopWrapper: 'header .bottom-wrap .left-wrap .main-menu',
		desktopMethod: 'appendTo',
		breakpoint: 1200
	})
}

function menuMobile () {
	let hambuger = $('.burger-bt-inner')
	let menuMB = $('.mobile-wrap')
	let searchBox = $('.searchbox-wrap')
	hambuger.on('click', function () {
		// console.log(1111);
		header.checkHeightHeader();
		$(this).toggleClass('active');
		menuMB.toggleClass('active');
		searchBox.removeClass('active');
	})

	$('.menu-list .has-dropdown').each(function () {
		$(this).append('<span class="icon-dropdown"></span>')
	})

	$('.menu-list .has-dropdown .icon-dropdown').on('click', function () {
		$(this).prev().slideToggle();
	})
}

function toggleSearch () {
	let button = $('.button-search')
	let searchBox = $('.searchbox-wrap')
	let close = $('.searchbox-wrap .button-close')
	let hambuger = $('.burger-bt-inner')
	let menuMB = $('.mobile-wrap')
	button.on('click', function () {
		searchBox.toggleClass('active');
		hambuger.removeClass('active');
		menuMB.removeClass('active');
	})
	close.on('click', function () {
		searchBox.toggleClass('active');
	})

}

function swiperInit () {
	var swiper = new Swiper('.section-awards-12 .swiper-container', {
		slidesPerView: 2,
		spaceBetween: 15,
		grid: {
			rows: 2,
			fill: 'row',
		},
		navigation: {
			nextEl: '.section-awards-12 .btn-next',
			prevEl: '.section-awards-12 .btn-prev',
		},
		breakpoints: {
			768: {
				slidesPerView: 3,
				spaceBetween: 30,
				grid: {
					rows: 2,
					fill: 'row',
				},
			},
			1024: {
				slidesPerView: 6,
				spaceBetween: 30,
				grid: {
					rows: 2,
					fill: 'row',
				},
			},
		}
	});
	$(".swiper-column-auto").each(function (index) {
		const $this = $(this);
		// Configuration flagsvideoSetting
		const config = {
			loop: $this.hasClass("swiper-loop"),
			touchMove: $this.hasClass("allow-touchMove") || true,
			mouseWheel: $this.hasClass("allow-mouseWheel") ? { forceToAxis: true } : false,
			autoHeight: $this.hasClass("auto-height"),
			hasVideo: $this.hasClass("auto-detect-video"),
			progressbar: $this.hasClass("progressbar"),
			time: $this.attr("data-time") || 3500,
			autoplay: $this.hasClass("autoplay"),
		};

		// Add unique identifier class
		$this.addClass(`swiper-column-auto-id-${index}`);

		// Create swiper with optimized options
		const swiper = new Swiper(`.swiper-column-auto-id-${index} .swiper-container`, {
			speed: 500,
			spaceBetween: 0,
			rewind: true,
			...(config.loop && {
				loop: true,
			}),
			...(config.autoplay && {
				autoplay: {
					delay: config.time,
				},
			}),
			slidesPerView: "auto",
			pagination: {
				el: `.swiper-column-auto-id-${index} .swiper-pagination`,
				clickable: true,
				...(config.progressbar && {
					type: 'progressbar',
				}),
			},
			navigation: {
				prevEl: `.swiper-column-auto-id-${index} .btn-prev`,
				nextEl: `.swiper-column-auto-id-${index} .btn-next`,
			},
			watchSlidesProgress: true,
			autoHeight: config.autoHeight,
			on: {
				init: function () {
					// Check slide count and add class if necessary
					const $swiperWrapper = $this; // $this refers to the outer .swiper-column-auto element
					const classList = $swiperWrapper.attr('class').split(' ');
					const columnClass = classList.find(cls => cls.startsWith('auto-') && cls.endsWith('-column'));
					if (columnClass) {
						const expectedColumns = parseInt(columnClass.split('-')[1], 10);
						if (!isNaN(expectedColumns)) {
							const actualSlides = this.slides.length; // 'this' refers to the Swiper instance here
							if (actualSlides < expectedColumns) {
								$swiperWrapper.addClass('less-than-expected-slides');
							}
						}
					}
				},
				slideChange: function () {
				},
			},
		});
	});
	var homeHightLights = new Swiper('.home-slide .swiper-container', {
		loop: true,
		autoplay: {
			delay: 5000,
			waitForTransition: true,
			disableOnInteraction: false,
		},
		slidesPerColumnFill: "row",
		watchSlidesProgress: true,
		watchSlidesVisibility: true,
		slidesPerView: 1,
		spaceBetween: 20,
		pagination: {
			el: '.home-slide .main-pagination',
			type: 'bullets',
			clickable: true,
		},
		navigation: {
			nextEl: '.home-slide .button-next',
			prevEl: '.home-slide .button-prev'
		},
		breakpoints: {
			768: {
				slidesPerView: 2,
				slidesPerGroup: 2,
			},
		}
	});
	var eventsDetailSlide = new Swiper('.om-events-detail .swiper-container', {
		loop: true,
		autoplay: {
			delay: 5000,
			waitForTransition: true,
			disableOnInteraction: false,
		},
		slidesPerColumnFill: "row",
		watchSlidesProgress: true,
		watchSlidesVisibility: true,
		slidesPerView: 1,
		spaceBetween: 20,
		pagination: {
			el: '.om-events-detail .main-pagination',
			type: 'bullets',
			clickable: true,
		},
		navigation: {
			nextEl: '.om-events-detail .button-next',
			prevEl: '.om-events-detail .button-prev'
		},
		breakpoints: {
			768: {
				slidesPerView: 2,
			},
			1024: {
				slidesPerView: 3,
				spaceBetween: 17,
			},
		}
	});
	var videoDetailSlide = new Swiper('.om-video-detail .swiper-container', {
		// loop: true,
		autoplay: {
			delay: 5000,
			waitForTransition: true,
			disableOnInteraction: false,
		},
		slidesPerColumnFill: "row",
		watchSlidesProgress: true,
		watchSlidesVisibility: true,
		slidesPerView: 1,
		spaceBetween: 20,
		pagination: {
			el: '.om-video-detail .main-pagination',
			type: 'bullets',
			clickable: true,
		},
		navigation: {
			nextEl: '.om-video-detail .button-next',
			prevEl: '.om-video-detail .button-prev'
		},
		breakpoints: {
			576: {
				slidesPerView: 2,
			},
			1024: {
				slidesPerView: 3,
				slidesPerColumn: 2,
			},
		}
	});
}

