function scrollToSection () {
	let elemHeader = $('header').outerHeight();
	let elemLink = $('.about-page .about-nav li a');
	elemLink.on('click', function (e) {
		e.preventDefault();
		var hash = this.hash;
		$('html, body').animate({
			scrollTop: $(hash).offset().top - elemHeader
		}, 1000);
	})

}

let aboutNav = {
	scrollToSection: () => {
		let elemHeader = $('header').outerHeight();
		let elemLink = $('.about-page .about-nav li a');
		elemLink.on('click', function (e) {
			e.preventDefault();
			var hash = this.hash;
			$('html, body').animate({
				scrollTop: $(hash).offset().top - elemHeader
			}, 1000);
		})
	},
	stickyTop: () => {
		let height = $('header').outerHeight();
		let navMB = $('.main-nav-mb');
		navMB.css('top', height);
	},
	toggleClass: () => {
		let button = $('.main-nav-mb .toggle');
		let nav = $('.main-nav-mb .about-nav');
		button.on('click', function () {
			$(this).toggleClass('active');
			nav.slideToggle();
		});
	},
	init: () => {
		if ($(window).width() <= 1024) {
			aboutNav.toggleClass();
		}
		aboutNav.stickyTop();
		aboutNav.scrollToSection();
	}
}

