function limitCheckboxGroup () {
	$('.form-group.qcheckboxes').each(function() {
		const $group = $(this);
		const $checkboxes = $group.find('input[type="checkbox"]');
		const maxSelection = 3;
		const textAlert = $('html').attr('lang') == 'en' ? 'You can only select a maximum of 3 options in this category.' : 'Bạn chỉ có thể chọn tối đa 3 tùy chọn trong danh mục này.';
		
		$checkboxes.on('change', function() {
			const checkedCount = $group.find('input[type="checkbox"]:checked').length;
			
			if (checkedCount > maxSelection) {
				$(this).prop('checked', false);
				alert(textAlert);
			}
		});
	});
}

