function initializeCountdown () {
	$(".countdown-time").each(function () {
		const endDateAttr = $(this).data("date-end");
		if (!endDateAttr) return;
		const endDate = new Date(endDateAttr).getTime();
		const $element = $(this);

		// Function to update the countdown
		function updateCountdown () {
			const now = new Date().getTime();
			const timeRemaining = endDate - now;

			if (timeRemaining > 0) {
				const days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24));
				const hours = Math.floor((timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
				const minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60));
				const seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000);
				$element.html(`
					<div class="countdown-time-item"><p><span class="count-number">${days.toString().padStart(2, "0")}</span><span class="count-text">Days</span></p></div>
					<div class="countdown-time-item"><p><span class="count-number">${hours.toString().padStart(2, "0")}</span><span class="count-text">Hours</span></p></div>
					<div class="countdown-time-item"><p><span class="count-number">${minutes.toString().padStart(2, "0")}</span><span class="count-text">Min</span></p></div>
					<div class="countdown-time-item"><p><span class="count-number">${seconds.toString().padStart(2, "0")}</span><span class="count-text">Sec</span></p></div>
				`);
			} else {
				$element.html(`
					<div class="countdown-time-item"><p><span class="count-number">${days.toString().padStart(2, "0")}</span><span class="count-text">Days</span></p></div>
					<div class="countdown-time-item"><p><span class="count-number">${hours.toString().padStart(2, "0")}</span><span class="count-text">Hours</span></p></div>
					<div class="countdown-time-item"><p><span class="count-number">${minutes.toString().padStart(2, "0")}</span><span class="count-text">Min</span></p></div>
					<div class="countdown-time-item"><p><span class="count-number">${seconds.toString().padStart(2, "0")}</span><span class="count-text">Sec</span></p></div>
				`);
			}
		}
		// Initial call to set the countdown immediately
		updateCountdown();
		// Update the countdown every second
		setInterval(updateCountdown, 1000);
	});
	setHeaderHeight()
}

function setHeaderHeight () {
	let height = $('header').outerHeight()
	document.documentElement.style.setProperty("--header-height", `${height}px`);
}
function appendCaptchaASP () {
	if (!$(".section-award-form").length) return;
	$('.section-award-form label').each(function () {
		const label = $(this);
		let html = label.html();
		if (html.includes('*')) {
			html = html.replace(/\*/g, '<span class="text-danger">*</span>');
			label.html(html);
		}
	})
	// Select the div element you want to observe
	const myDiv = document.querySelector("#ctl00_mainContent_ctl02_pnlFormWizard");
	// Create a new MutationObserver object
	const observer = new MutationObserver(function (mutations) {
		mutations.forEach(function (mutation) {
			appendCaptcha();
		});
	});
	// Configure the observer to listen for changes to the "class" attribute
	const config = { attributes: true, characterData: true, childList: true };
	// Start observing the target div element
	observer.observe(myDiv, config);
	function appendCaptcha () {
		$(".form-group.frm-captcha").appendTo(".wrap-form-submit");
		$(".form-group.frm-btnwrap").appendTo(".wrap-form-submit");
	}
	appendCaptcha();
}

function loopItemHeight () {
	$('.item-var-height').each(function () {
		let height = $(this).height();
		$(this).closest('.item-wrap-height').attr('style', '--height:' + height + 'px');
	})
}

function hoverAwards6 () {
	$('.section-awards-6 .col-content .list-tab-awards li').on('mouseenter', function () {
		let index = $(this).data('index');
		$(this).addClass('active').siblings().removeClass('active');
		$('.section-awards-6 .col-image img[data-index="' + index + '"]').addClass('active').siblings().removeClass('active');
	})
}